





















import graceSlider from '../../graceUI/components/graceSlider.vue';
export default {
	data() {
		return {
			ageMin : 20,
			ageMax : 55,
			priceMin : 100,
			priceMax : 512
		}
	},
	methods: {
		sliding1:function(e){
			var min = e.min;
			var max = e.max;
			this.ageMin = min;
			this.ageMax = max;
		},
		sliding2: function (e) {
			var min = e.min;
			var max = e.max;
			this.priceMin = min;
			this.priceMax = max;
		}
	},
	components : {
		graceSlider
	}
}
