






export default {
	name: "graceSlider",
	props: {
		min: {
			type: Number,
			default: 0
		},
		max: {
			type: Number,
			default: 100
		},
		val1: {
			type: Number,
			default: 0
		},
		val2: {
			type: Number,
			default: 50
		},
		blockColor : {
			type : String,
			default : "#FFFFFF"
		},
		bgColor:{
			type: String,
			default: "#F2F3F4"
		}
	},
	methods: {
		change1 : function(e){
			this.val1 = e.detail.value;
			this.changeBase();
		},
		change2: function (e) {
			this.val2 = e.detail.value;
			this.changeBase();
		},
		changeBase : function(){
			var v1 = this.val1;
			var v2 = this.val2;
			if(v1 > v2){
				this.val2 = v1;
				this.val1 = v2;
			}
			this.$emit('sliding', { min: this.val1, max: this.val2});
		}
	}
}
